<template >
  <div>
    <div class="yytitle">
       <div class="yytitle_left">
         <img src="https://images.yyhealth.com//technician/201903/2019031415260585775c8a020ddc213.png" alt="">
       </div>
       <div class="yytitle_right">
          <span>远元健康<br>为您的生命加分</span>
          <!-- <span>为您的生命加分</span> -->
       </div>
    </div>
    <van-cell-group>
      <!-- <van-field
        v-model="username"
        label="用户名"
        left-icon="phone-o"
        placeholder="请输入用户名"
        right-icon="question-o"
      /> -->
      <van-field
        v-model="phone"
        left-icon="phone-o"
        placeholder="请输入手机号"
        :error-message="error_message"
        :error="is_error"
        @input="tel"
        maxlength = 11
      
      />
      <van-field v-model="sms" center clearable  left-icon="envelop-o" placeholder="请输入短信验证码">
        <van-button slot="button" round size="small" type="primary"  @click="getTelCode">获取验证码<span v-if="is_clock">({{lastTime}}s)</span></van-button>
        <!-- :class="!canClick?'disabled': ''" -->
      </van-field>
    </van-cell-group>
    <van-button block size="normal" round id="loginBtn" type="info">登陆</van-button>
  </div>
</template>
<script>
import $ from "jquery";
import {
  Button,
  Field,
  CellGroup
} from "vant";

export default {
  components: {
    [Button.name]: Button,  
    [Field.name]: Field,  
    [CellGroup.name]: CellGroup  
  },
  data() {
    return {
      username: "",
      phone: "",
      error_message: "",
      is_error: false,
      sms: "",
      lastTime:5,
      is_clock:false,
      canClick:true,
    };
  },
  mounted() {
    // $("input").on("click", function() {
    //   setTimeout(function() {
    //     document.body.scrollTop = document.body.scrollHeight;
    //   }, 300);
    // });
  },
  methods:{
      //手机号码
      tel(){
        if(this.phone){
          this.is_error=false
          this.error_message =""
        }
      },
      //获取验证码
      getTelCode(){
        if(this.phone.length!=0){
            // console.log(this.phone)
             var reg=/^1[3456789]\d{9}$/;
              if(!reg.test(this.phone)){
                this.error_message = "请输入正确的手机号"
              }else{
                if (!this.canClick) return  //改动的是这两行代码
                this.canClick = false
                this.is_clock=true
                //倒计时
                let clock = window.setInterval(()=>{
                  this.lastTime--
                  if(this.lastTime<0){
                    window.clearInterval(clock)
                      this.is_clock=false
                      this.canClick = true
                      this.lastTime = 5
                  }
                },1000)
                
              }
        }else{
          this.is_error= true
        }
      },
      login(){
        
      }
  }
};
</script>

<style>
#loginBtn {
  width: 7.2rem;
  margin: 0 0.15rem;
}
.van-cell__value--alone {
    padding-left: 0.14rem;
}
.yytitle{
  height: 2.6rem;
  width: 100%;
  background: #fff;
  padding-left: .28rem;
  padding-top:.5rem;
  box-sizing: border-box;
}
.yytitle_left{
  float: left;
  margin-right:.2rem; 
}
.yytitle_left img{
  height: 1.43rem;
  width:1.16rem;

}
.yytitle_right{
  float: left;
  padding-top:.2rem;
  height: 1.43rem;
  font-size: 16px;
}
.disabled:hover{
 /* background-color: #ddd; */
 /* border-color: #ddd; */
 /* color:#57a3f3; */

 cursor: not-allowed; 
}
</style>

